*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  line-height: 125%;
  background-color: #181818; }

a {
  text-decoration: none; }

li {
  list-style: none; }

.icon-link {
  font-size: 20px;
  color: #aaa;
  transition: color 0.3s ease;
  &:hover {
    color: #fff; } }
